@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300&display=swap');

html {
    height: 100%;
}

body {
    margin: 0px;
    height: 100%;
}

#root {
    width: 100%;
    height: 100%;
}

.App {
    display: inline-flex;
    height: 100%;
    width: 100%;
}

#sidebar {
    width: 250px;
    height: 100%;
    background: linear-gradient(to right, #5A60FF , #628CFF );
    color: #E3E3E3;
    padding-left: 20px;
}

#board {
    width: calc(100% - 250px);
    height: calc(100% - 10px);
    background: #fafafa;
    padding-left: 20px;
    padding-top: 10px;
}